#menu-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row-reverse;
    position: fixed;
    width:100vw;
    height:100vh;
    z-index: 10;
    
    transition: 0.1s;
}
#main_menu_container_{
    background:linear-gradient(rgb(16, 16, 16),rgb(22, 22, 22),black);
    width:60%;
    height:100vh;
    transition: 0.2s;
}
#overflow_area_menu_{
    height:100vh;
    width:40vw;
    
}
#main_menu_container_ article{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding:2%;
    margin:4% 0%;
}
#main_menu_container_ article #text_container{
    padding:0px;
    width:70%;


}
#main_menu_container_ article #text_container h2{
    color:white;
}
#main_menu_container_ article #image_container{
    width:10vw;
}
#main_menu_container_ article #image_container img{
    width:10vw;
    height:10vw;
    width:100%;
    border-radius: 500px;
    border:2px solid white;
}
#menu_holder ul {
    text-align: center;
   
}
#menu_holder ul li{
    padding:3%;
    font-size:1rem;
    margin:5%;
    
}
.selected{
    border: 2px solid white;
    border-radius:50px;
    color:black;
    font-weight: 900;
}
.selected li{
    color:black;
}

#menu_footer_container{
    text-align: center;
    font-size:smaller;
    color:rgb(156, 156, 156);
    border-top:1px solid rgb(124, 124, 124);
    padding-top:5%;
}
#menu_footer_container p{
    color:rgb(147, 147, 147);
}
.menu-visible{
    display: block;
    background:linear-gradient(rgba(19, 19, 19, 0.842),black);
}
.menu-hidden{
   display: none;
   z-index:-1;
 
}
.overlay-visible {
    display: block; /* Ensure the overlay is visible */
  }
  
  .overlay-hidden {
    display: none; /* or visibility: hidden; */
  }