#ActivityCard_container article *{
    margin:0px;
    padding:0px;
}
#ActivityCard_container{
    width:95%;
    margin:15% auto;
    padding:0% 3%;
    border-radius: 10px;
    outline:2px solid rgb(125, 125, 125);
}
#ActivityCard_container h3{
    font-size: 6vw;
    text-align: center;
    padding:2%;
    font-weight: 500;
    color:white;
    margin-bottom: 2%;
}
#ActivityCard_container article #image_container img{
    width:100%;
    border-radius: 10px;
    height:35vh;
    outline:0px solid rgb(125, 125, 125);
}
#ActivityCard_container article #text_container {

    width: 100%;
}
#ActivityCard_container article #text_container ul li{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    list-style-type: none;
    margin:2% 0px;
}
#ActivityCard_container article #text_container ul li P{
    margin:0px 5px;
}
#ActivityCard_container article #text_container ul li h4{
    display: flex;
    align-items: center;
    color:white;
}
#ActivityCard_container #btn_container{
    margin:5%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}